.evp.evp-table {
  > thead {
    tr:nth-child(2n + 1) {
      background-color: #fafafa;
    }
  }

  > tbody.zebra {
    .evp-table-column-record-row:nth-child(2n) {
      background-color: azure;
    }

    .evp-table-column-record-row:nth-child(2n + 1) {
      background-color: #fff;
    }
  }
}

.evp-table-column-record-row,
.evp-table-column-headers-row {
  position: relative;

  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0.5px;
    background-color: #ebedf2;
  }
}

